import {
    useMutation,
    UseMutationOptions,
    UseMutationResult
} from '@tanstack/react-query';
import {UserRegistrationBuiltInCommand, EmptyResult} from '../../types';
import {useCreateAuthenticatedFetch} from '../useCreateAuthenticatedFetch';
import baseUrl from '../baseUrl';
import {handleResponse} from '../handleResponse';

const useUserRegistrationBuiltIn = (
    options?: UseMutationOptions<
        EmptyResult,
        Error,
        UserRegistrationBuiltInCommand
    >
): UseMutationResult<EmptyResult, Error, UserRegistrationBuiltInCommand> => {
    const authenticatedFetch = useCreateAuthenticatedFetch();

    return useMutation({
        mutationFn: async (command: UserRegistrationBuiltInCommand) => {
            const response = await authenticatedFetch(
                `${baseUrl}/api/auth/user-registration`,
                {
                    method: 'POST',
                    headers: {'Content-Type': 'application/json'},
                    body: JSON.stringify(command)
                }
            );
            return handleResponse(response);
        },
        ...options
    });
};

export default useUserRegistrationBuiltIn;
